olor:#666}.react-contexify__theme--light .react-contexify__item__icon{margin-right:8px;width:12px;text-align:center}@keyframes react-contexify__popIn{0%{transform:scale(0)}100%{transform:scale(1)}}@keyframes react-contexify__popOut{0%{transform:scale(1)}100%{transform:scale(0)}}.react-contexify__will-enter--pop{animation:react-contexify__popIn .3s cubic-bezier(0.51, 0.92, 0.24, 1.2)}.react-contexify__will-leave--pop{animation:react-contexify__popOut .3s cubic-bezier(0.51, 0.92, 0.24, 1.2)}@keyframes react-contexify__zoomIn{from{opacity:0;transform:scale3d(0.3, 0.3, 0.3)}50%{opacity:1}}@keyframes react-contexify__zoomOut{from{opacity:1}50%{opacity:0;transform:scale3d(0.3, 0.3, 0.3)}to{opacity:0}}.react-contexify__will-enter--zoom{transform-origin:top left;animation:react-contexify__zoomIn .4s}.react-contexify__will-leave--zoom{animation:react-contexify__zoomOut .4s}@keyframes react-contexify__fadeIn{from{opacity:0}to{opacity:1}}@keyframes react-contexify__fadeOut{from{opacity:1}to{opacity:0}}.react-contexify__will-enter--fade{animation:react-contexify__fadeIn .3s ease}.react-contexify__will-leave--fade{animation:react-contexify__fadeOut .3s ease}@keyframes react-contexify__flipInX{from{transform:perspective(400px) rotate3d(1, 0, 0, 90deg);animation-timing-function:ease-in}40%{transform:perspective(400px) rotate3d(1, 0, 0, -20deg);animation-timing-function:ease-in}60%{transform:perspective(400px) rotate3d(1, 0, 0, 10deg)}80%{transform:perspective(400px) rotate3d(1, 0, 0, -5deg)}to{transform:perspective(400px)}}@keyframes react-contexify__flipOutX{from{transform:perspective(400px)}30%{transform:perspective(400px) rotate3d(1, 0, 0, -20deg);opacity:1}to{transform:perspective(400px) rotate3d(1, 0, 0, 90deg);opacity:0}}.react-contexify__will-enter--flip{backface-visibility:visible !important;animation:react-contexify__flipInX .65s}.react-contexify__will-leave--flip{animation:react-contexify__flipOutX .65s;backface-visibility:visible !important}
